body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin-left: auto;
  margin-right: auto;
  background: url(https://images.unsplash.com/photo-1542831371-29b0f74f9713?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxzZWFyY2h8NHx8Y29kaW5nfGVufDB8fDB8fA%3D%3D&w=1000&q=80);
  background-repeat: no-repeat;
  background-size: cover;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#react-container {
  filter: drop-shadow(0 0 0.75rem rgba(242, 235, 237, 0.176));
  padding: 20px;
  width: 400px;
  border-radius: 20%;
  background-color: #fff;
}

header {
  text-align: center;
}

section {
  width: 70%;
  margin-left: auto;
  margin-right: auto;
}

h2,
p {
  text-align: center;
}

li {
  list-style: upper-alpha;
  border-radius: 10px;
  padding: 5px;
  list-style-position: inside;
}

a {
  text-decoration: none;
  color: black;
  background-color: white;
  border-radius: 10px;
  padding: 5px;

}

a:hover {
  filter: drop-shadow(0 0 0.100rem rgba(16, 4, 7, 0.176));
  border-radius: 10px;
  padding: 5px;
}

.visible {
  display: block;
}

.invisible {
  display: none;
}

.answer,
.button {
  display: none;
}

.disabled {
  pointer-events: none;
}

.correct {
  color: green;
}

.incorrect {
  color: red;
}

.green a::before {
  background: green;
}

.red a::before {
  background: red;
}

button {
  background-color: black;
  color: white;
  border-radius: 5px;
}

button:hover {
 background-color: rgb(33, 33, 33);
}